<template>
  <ec-container height="100%" color-code="001" class="info" title="基地单元信息维护" show-title>
    <ec-container-body>
      <ec-panel :auto="false">
        <ec-panel-toolbar>
          <ec-query-panel :defaultFold="true" @query="onQuery" @reset="resetHandle">
            <ec-query-item label="基地单元名称">
              <a-input v-model.trim="query.baseCellName" placeholder="请输入基地单元名称" />
            </ec-query-item>
          </ec-query-panel>
          <ec-divider />
        </ec-panel-toolbar>
        <ec-panel-body>
          <ec-v-layout :auto="false">
            <!-- 表格工具栏 -->
            <ec-v-item height="auto">
              <ec-panel-toolbar>
                <ec-panel-item-left v-if="appConfig.orgLevelEk !== '1'">
                  <a-button type="primary" @click="handleUpdate(null)">新增</a-button>
                  <a-button type="primary" ghost @click="onDel">删除</a-button>
                </ec-panel-item-left>
                <ec-panel-item-right>
                  <div class="unit-box">
                    <span>集中交易年度：{{ busiYear }}年</span>
                    <div class="select">
                      重量单位：
                      <ec-select showSearch v-model.trim="weightUnit">
                        <a-select-option v-for="item in weightUnitList" :key="item.enumKey">
                          {{ item.name }}
                        </a-select-option>
                      </ec-select>
                    </div>
                    <div class="select">
                      面积单位：
                      <ec-select showSearch v-model.trim="areaUnit">
                        <a-select-option v-for="item in areaUnitList" :key="item.enumKey">
                          {{ item.name }}
                        </a-select-option>
                      </ec-select>
                    </div>
                  </div>
                </ec-panel-item-right>
              </ec-panel-toolbar>
            </ec-v-item>
            <ec-v-item v-if="appConfig.orgLevelEk !== '1'" height="auto">
              <div class="side-info">
                <p>
                  <strong>辅助信息</strong>国家局设置新建单元数据为：<strong>{{
                    sideInfo.baseUnitCnt
                  }}</strong
                  >个，已增加：<strong>{{ sideInfo.baseUnitCntSet }}</strong
                  >个，剩余：<strong>{{ sideInfo.baseUnitCntUnSet }}</strong
                  >个。
                </p>
              </div>
            </ec-v-item>
            <!-- 列表 -->
            <ec-v-item>
              <ec-table
                ref="tableRef"
                :loading="loading"
                show-overflow
                height="100%"
                :auto="false"
                @page-action="pageAction"
                :pagination="pagination"
                :data="tableData"
                @checkbox-all="selectAllEvent"
                @checkbox-change="selectRowChange"
              >
                <ec-table-col type="checkbox" width="60" align="center"></ec-table-col>
                <ec-table-col type="seq" title="序号" width="50" align="center" />
                <ec-table-col field="provOrgName" title="省公司" min-width="150" align="left" />
                <ec-table-col field="cityOrgName" title="市公司" min-width="150" align="left" />
                <ec-table-col field="provDivisionName" title="省" min-width="150" align="left" />
                <ec-table-col
                  field="cityDivisionName"
                  title="地(州、市)"
                  min-width="150"
                  align="left"
                />
                <ec-table-col
                  field="counDivisionName"
                  title="县(区、市)"
                  min-width="150"
                  align="left"
                />
                <ec-table-col
                  field="baseCellCode"
                  title="基地单元编码"
                  min-width="150"
                  align="left"
                />
                <ec-table-col
                  field="baseCellName"
                  title="基地单元名称"
                  min-width="150"
                  align="left"
                />
                <ec-table-col
                  field="linkInduProvOrgName"
                  title="省级工业公司"
                  min-width="150"
                  align="left"
                />
                <ec-table-col
                  field="linkInduOrgName"
                  title="对口工业企业"
                  min-width="150"
                  align="left"
                />
                <ec-table-col field="leafTypeCode" title="烟叶类型" min-width="150" align="left">
                  <template #default="{ row }">
                    <div>
                      {{ row.leafTypeCode | getNameBy(leafTypeObj) }}
                    </div>
                  </template>
                </ec-table-col>
                <ec-table-col field="fieldArea" title="基本烟田面积" min-width="150" align="left">
                  <template #default="{ row }">
                    <div>{{ row.fieldArea | amountFormat(areaUnit) }}</div>
                  </template>
                </ec-table-col>
                <ec-table-col field="plantArea" title="种植面积" min-width="150" align="left">
                  <template #default="{ row }">
                    <div>{{ row.plantArea | amountFormat(areaUnit) }}</div>
                  </template>
                </ec-table-col>
                <ec-table-col field="buyWeight" title="收购量" min-width="150" align="left">
                  <template #default="{ row }">
                    <div>{{ row.buyWeight | amountFormat(weightUnit) }}</div>
                  </template>
                </ec-table-col>
                <ec-table-col
                  field="techSuppOrg"
                  title="技术依托单位"
                  min-width="150"
                  align="left"
                />
                <ec-table-col field="unitProp" title="单元属性" min-width="150" align="left">
                  <template #default="{ row }">
                    <div>
                      {{
                        row.unitProp
                          | getNameBy(unitPropList, {
                            key: 'enumKey',
                            label: 'name',
                          })
                      }}
                    </div>
                  </template>
                </ec-table-col>
                <ec-table-col field="linkUpState" title="状态" width="100" align="left">
                  <template #default="{ row }">
                    <div>
                      {{ row.linkUpState | getNameBy(linkUpStateObj) }}
                    </div>
                  </template>
                </ec-table-col>
                <ec-table-col
                  v-if="appConfig.orgLevelEk === '1' || appConfig.orgLevelEk === '2'"
                  title="操作"
                  width="80"
                  align="right"
                >
                  <template #default="{ row }">
                    <a-button type="link" @click="onEdit(row)">编辑</a-button>
                  </template>
                </ec-table-col>
              </ec-table>
            </ec-v-item>
          </ec-v-layout>
        </ec-panel-body>
      </ec-panel>
      <business-org-manage-edit
        ref="editRef"
        :orgList="orgList"
        :busiYear="busiYear"
        :leafTypeList="leafTypeList"
        :sideInfo="sideInfo"
        @onQuery="onQuery"
      ></business-org-manage-edit>
    </ec-container-body>
  </ec-container>
</template>

<script>
import BusinessOrgManageEdit from './mainTainEdit.vue';
import _ from 'lodash';

export default {
  name: 'PanelSupple',
  components: {
    BusinessOrgManageEdit,
  },
  data() {
    return {
      isEdit: false,
      query: {}, // 查询数据
      // 重量单位
      weightUnitList: [],
      // 面积单位
      areaUnitList: [],
      // 当前重量单位
      weightUnit: '500000',
      // 当前面积单位
      areaUnit: '10000',
      // 单元属性
      unitPropList: [],
      // 基地单元名称
      linkUpState: [],
      // 辅助信息
      sideInfo: {
        baseUnitCnt: '',
        baseUnitCntSet: '',
        baseUnitCntUnSet: '',
      },
      loading: false,
      tableData: [], // 表格数据
      pagination: {
        // 分页
        pageNumber: 1,
        pageSize: 10,
        total: 0,
      },
      // 业务年度
      busiYear: undefined,
      // 商业单位
      orgList: [],
      // 烟叶类型
      leafTypeList: [],
      // 应用编码
      appCode: undefined,
    };
  },
  computed: {
    leafTypeObj() {
      return this.leafTypeList.map((item) => {
        return {
          key: item.leafTypeCode,
          title: item.leafTypeName,
        };
      });
    },
    linkUpStateObj() {
      return this.linkUpState.map((item) => {
        return {
          key: item.enumKey,
          title: item.name,
        };
      });
    },
    appConfig() {
      return this.$store.getters.getAppConfig;
    },
  },

  created() {
    // 通过路由获取appCode
    const { appCode } = this.$router.history.current.query;
    this.appCode = appCode || 'CM';
    this.busiYear = this.appConfig?.yearValueMap?.CENTER_YEAR;
    this.query.busiYear = this.appConfig?.yearValueMap?.CENTER_YEAR;
    this.getAuxiliaryInfo();
    this.handleQueryLeafTypeList();
    this.handleGetEnum();
    this.handleQueryOrgList();
  },
  methods: {
    // 查询枚举值
    handleGetEnum() {
      const params = {
        enumTypeCodes: ['WEIGHT_UNIT', 'AREA_UNIT', 'BASE_UNIT_NATURE', 'LINK_UP_STATE'],
      };
      this.$apis.bim.enumType.query(params).then((res) => {
        const enumMap = res?.enumMap || {};
        this.weightUnitList = enumMap.WEIGHT_UNIT || [];
        this.areaUnitList = enumMap.AREA_UNIT || [];
        this.unitPropList = enumMap.BASE_UNIT_NATURE || [];
        this.linkUpState = enumMap.LINK_UP_STATE || [];
      });
    },
    // 查询烟叶类型
    handleQueryLeafTypeList() {
      this.$apis.bim.leafType.query({}).then((res) => {
        this.leafTypeList = res?.rows || [];
      });
    },
    // 重置
    resetHandle() {
      this.query = {
        busiYear: this.busiYear,
      };
      this.onQuery();
    },
    // 查询商业单位
    handleQueryOrgList() {
      const params = {
        busiYear: this.busiYear,
        orgTypeEk: 'TOBACCO',
        orgLevelEk: '4',
        parentBizCode: this.appConfig?.bizCode,
      };
      this.$apis.bim.org.queryBy(params).then((res) => {
        this.orgList = res.rows || [];
      });
    },
    // 查询
    async pageAction(pageNo, pageSize) {
      this.pageNo = pageNo;
      // 加载中
      this.loading = true;
      // 翻页要带上当前查询条件
      this.getData({ pageNumber: pageNo, pageSize, ...this.query })
        .then((res) => {
          this.pagination.total = res.totalRows;
          this.tableData = res.rows;
          this.loading = false;
        })
        .catch((err) => {
          this.loading = false;
          console.log(err);
        });
    },
    // 查询
    onQuery() {
      this.$refs.tableRef.pageAction(1, 10);
    },
    // 获取表格数据
    getData(data) {
      return this.$apis.apm.baseMaintain.query(data);
    },
    // 获取辅助信息
    getAuxiliaryInfo() {
      return this.$apis.apm.baseMaintain.auxiliaryInfo().then((res) => {
        this.sideInfo = res;
      });
    },
    // 新增
    handleUpdate() {
      this.$refs.editRef.show();
    },
    // 编辑
    onEdit(data) {
      this.$refs.editRef.show(_.cloneDeep(data));
    },
    editRowEvent() {
      this.isEdit = !this.isEdit;
      const $table = this.$refs.xTable;
      $table.setActiveRow();
    },
    // 按钮删除
    onDel() {
      if (!this.selectedRows || this.selectedRows.length <= 0) {
        this.$message.error('请选择一条数据再删除');
        return;
      }
      this.$confirm({
        title: '是否执行该操作?',
        content: () => (
          <div style="color:red;">
            是否确认删除选中的数据，确认请点击'确认'按钮，取消点击'取消'按钮。
          </div>
        ),
        onOk: () => {
          this.loading = true;
          const idList = [];
          for (let i = 0; i < this.selectedRows.length; i++) {
            idList.push(this.selectedRows[i].leafColorId);
          }
          const selectList = this.selectedRows.map((item) => {
            return item.bmUnitLinkRecId;
          });
          console.log(selectList);
          this.$apis.apm.baseMaintain
            .delete({ bmUnitLinkRecIds: selectList })
            .then((res) => {
              if (res.result) {
                this.loading = false;
                this.onQuery();
                this.$message.success('操作成功');
              } else {
                this.$message.error('操作失败');
              }
            })
            .catch((error) => {
              this.loading = false;
              if (error) {
                this.$message.error(error.message || '操作失败');
              } else {
                this.$message.error('操作失败');
              }
            });
        },
      });
    },
    // 选中所有列表
    selectAllEvent({ records }) {
      this.selectedRows = records;
    },
    // 选中列表
    selectRowChange({ records }) {
      this.selectedRows = records;
    },
  },
};
</script>
<style lang="less" scoped>
/deep/.ec-table-selected {
  display: none;
}
.side-info {
  padding: 4px 10px;
  border-radius: 4px;
  border: 1px solid #d5f0ff;
  background: #e6f7ff;
  p {
    margin-bottom: 0;
    color: #8e9496;
    font-size: 14px;
    strong {
      color: #3092fe;
    }
  }
}
.unit-box {
  display: flex;
  align-items: center;
  .select {
    margin-left: 20px;
    /deep/ .ant-select {
      width: 100px;
    }
  }
}
</style>
