<template>
  <div>
    <ec-drawer
      :title="formData.bmUnitLinkRecId ? '编辑' : '新增'"
      :visible="visible"
      size="small"
      @close="handleCancel"
      :is-confirm="true"
    >
      <ec-container height="auto">
        <ec-container-body>
          <ec-panel>
            <ec-panel-item>
              <ec-v-item height="auto">
                <div class="side-info">
                  <p>
                    <strong>辅助信息</strong>国家局设置新建单元数据为：<strong>{{
                      sideInfo.baseUnitCnt
                    }}</strong
                    >个，已增加：<strong>{{ sideInfo.baseUnitCntSet }}</strong
                    >个，剩余：<strong>{{ sideInfo.baseUnitCntUnSet }}</strong
                    >个。
                  </p>
                </div>
              </ec-v-item>
              <ec-form ref="ruleForm" :model="formData" :rules="rules">
                <a-row type="flex" :gutter="12">
                  <a-col :span="12">
                    <a-form-model-item label="商业单位" prop="counOrgCode">
                      <select-picker
                        join
                        title="选择商业单位"
                        :lazy="false"
                        :search="false"
                        :fetchData="fetchGetTree"
                        placeholder="请选择商业名称"
                        v-model="formData.counOrgCode"
                        :beforeOk="onBeforeOkOrg"
                        @ok="onSelOrg"
                      />
                    </a-form-model-item>
                  </a-col>
                  <a-col :span="12">
                    <a-form-model-item label="所在行政区划" prop="counDivisionCode">
                      <division-picker
                        join
                        v-model.trim="formData.counDivisionCode"
                        :beforeOk="onBeforeOkDivision"
                        @ok="onSelDivision"
                        placeholder="请选择"
                      />
                    </a-form-model-item>
                  </a-col>
                  <a-col :span="12">
                    <a-form-model-item label="基地单元编码" prop="baseCellCode">
                      <a-input
                        v-model="formData.baseCellCode"
                        placeholder="保存后自动生成"
                        autocomplete="off"
                        disabled
                      />
                    </a-form-model-item>
                  </a-col>
                  <a-col :span="12">
                    <a-form-model-item label="基地单元名称" prop="baseCellName">
                      <a-input
                        v-model="formData.baseCellName"
                        placeholder="请输入基地单元名称"
                        autocomplete="off"
                      />
                    </a-form-model-item>
                  </a-col>
                  <a-col :span="12">
                    <a-form-model-item label="烟叶类型" prop="leafTypeCode">
                      <ec-select
                        showSearch
                        allowClear
                        placeholder="请选择烟叶类型"
                        v-model.trim="formData.leafTypeCode"
                      >
                        <a-select-option v-for="item in leafTypeList" :key="item.leafTypeCode">
                          {{ item.leafTypeName }}
                        </a-select-option>
                      </ec-select>
                    </a-form-model-item>
                  </a-col>
                  <a-col :span="12">
                    <a-form-model-item label="技术依托单位" prop="techSuppOrg">
                      <a-input
                        v-model="formData.techSuppOrg"
                        autocomplete="off"
                        placeholder="请输入技术依托单位"
                      />
                    </a-form-model-item>
                  </a-col>
                  <a-col :span="12">
                    <a-form-model-item label="基本烟田面积（万亩）" prop="fieldAreaTemp">
                      <a-input-number
                        v-model="formData.fieldAreaTemp"
                        placeholder="请输入基本烟田面积"
                        autocomplete="off"
                      />
                    </a-form-model-item>
                  </a-col>
                  <a-col :span="12">
                    <a-form-model-item label="种植面积（万亩）" prop="plantAreaTemp">
                      <a-input-number
                        v-model="formData.plantAreaTemp"
                        placeholder="请输入种植面积"
                        autocomplete="off"
                      />
                    </a-form-model-item>
                  </a-col>
                  <a-col :span="12">
                    <a-form-model-item label="收购量（万担）" prop="buyWeightTemp">
                      <a-input-number
                        v-model="formData.buyWeightTemp"
                        placeholder="请输入收购量"
                        autocomplete="off"
                      />
                    </a-form-model-item>
                  </a-col>

                  <a-col :span="12">
                    <a-form-model-item label="创建年度" prop="createYear">
                      <a-input-number v-model="formData.createYear" disabled />
                    </a-form-model-item>
                  </a-col>
                </a-row>
              </ec-form>
            </ec-panel-item>
          </ec-panel>
        </ec-container-body>
        <ec-container-footer>
          <a-button type="primary" @click="handleSubmit">保存</a-button>
          <a-button @click="handleCancel">关闭</a-button>
        </ec-container-footer>
      </ec-container>
    </ec-drawer>
  </div>
</template>
<script>
import { SelectPicker, DivisionPicker } from '@euler-material/ecm-ui';
import { getSelectedBy, treeFormatList } from '@euler-material/ecm-utils';

export default {
  name: 'BusinessOrgManageEdit',
  components: {
    SelectPicker,
    DivisionPicker,
  },
  props: {
    busiYear: {
      type: Number,
      default: 0,
    },
    orgList: {
      type: Array,
      default: () => [],
    },
    leafTypeList: {
      type: Array,
      default: () => [],
    },
    sideInfo: {
      type: Object,
      default: () => {},
    },
  },

  data() {
    return {
      visible: false,
      formData: {
        citcOrgCode: '',
        counOrgCode: '',
      },
      rules: {
        counOrgCode: { required: true, message: '请选择商业单位' },
        counDivisionCode: { required: true, message: '请选择所在行政区划' },
        baseCellName: { required: true, message: '请输入基地单元名称' },
        leafTypeCode: { required: true, message: '请选择烟叶类型' },
        fieldAreaTemp: { required: true, message: '请输入基本烟田面积' },
        plantAreaTemp: { required: true, message: '请输入种植面积' },
        buyWeightTemp: { required: true, message: '请输入收购量' },
      },
      // 收购量初始值
      buyWeightTemp: undefined,
    };
  },
  methods: {
    show(data) {
      this.visible = true;
      if (data) {
        this.formData = data;
        this.$set(this.formData, 'fieldAreaTemp', Number(this.formData.fieldArea) / 10000);
        this.$set(this.formData, 'plantAreaTemp', Number(this.formData.plantArea) / 10000);
        this.$set(this.formData, 'buyWeightTemp', Number(this.formData.buyWeight) / 500000);
        this.buyWeightTemp = data.buyWeight;
      } else {
        this.formData.busiYear = this.busiYear;
        this.formData.createYear = this.busiYear;
      }
    },
    // 设置行政
    onSelDivision(data) {
      const curDivision = data?.selectOption;
      const treeData = data?.treeData || [];
      const keys = getSelectedBy(treeData, curDivision.key);
      this.formData.provDivisionCode = keys[1];
      this.formData.cityDivisionCode = keys[2];
      this.formData.counDivisionCode = keys[3];
    },
    // 设置组织
    onSelOrg(data) {
      const curOrg = data?.selectOption;
      const treeData = data?.treeData || [];
      const keys = getSelectedBy(treeData, curOrg.key);
      this.formData.provOrgCode = keys[0];
      this.formData.cityOrgCode = keys[1];
      this.formData.counOrgCode = keys[2];
    },
    // 获取树组件数据
    fetchGetTree() {
      return new Promise((resolve) => {
        const replaceFields = {
          pkey: 'parentBizCode',
          title: 'orgName',
          key: 'bizCode',
          children: 'children',
        };
        const data = treeFormatList(this.orgList, replaceFields);
        resolve(data);
      });
    },
    handleSubmit() {
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          this.formData.fieldArea = Number(this.formData.fieldAreaTemp) * 10000;
          this.formData.plantArea = Number(this.formData.plantAreaTemp) * 10000;
          this.formData.buyWeight = Number(this.formData.buyWeightTemp) * 500000;
          this.formData.ifUpdateBuyWeight =
            this.buyWeightTemp === this.formData.buyWeight ? undefined : '1';

          $ec.showLoading();
          if (this.formData.bmUnitLinkRecId) {
            this.$apis.apm.baseMaintain.update(this.formData).then(() => {
              $ec.hideLoading();
              this.$emit('onQuery');
              this.$message.success('保存成功！');
              this.handleCancel();
            });
          } else {
            this.$apis.apm.baseMaintain.add(this.formData).then(() => {
              $ec.hideLoading();
              this.$emit('onQuery');
              this.$message.success('保存成功！');
              this.handleCancel();
            });
          }
        } else {
          this.$message.error('验证不通过');
        }
      });
    },
    // 选择商业单位前
    onBeforeOkOrg(data) {
      if (data.selectOption?.orgLevelEk !== '4') {
        this.$message.error('请选择县级商业单位');
        return false;
      }
    },
    // 选择行政区划前
    onBeforeOkDivision(data) {
      if (data.selectOption?.divisionLevel !== '4') {
        this.$message.error('请选择县级行政区划');
        return false;
      }
    },
    // 取消
    handleCancel() {
      this.visible = false;
      this.$refs.ruleForm.resetFields();
    },
  },
};
</script>
<style lang="less" scoped>
.side-info {
  padding: 4px 10px;
  border-radius: 4px;
  border: 1px solid #d5f0ff;
  background: #e6f7ff;
  p {
    margin-bottom: 0;
    color: #8e9496;
    font-size: 14px;
    strong {
      color: #3092fe;
    }
  }
}
</style>
